e__position{margin:10px 0 !important}.experience__company{font-size:13px;font-style:italic;color:#999;margin-bottom:0}.experience__date{font-size:13px;font-style:italic;margin-bottom:10px;color:#999}.experience__hr{height:1px;background-color:#eff0f2;width:100%}.experience__form-item:after{visibility:hidden;display:block;font-size:0;content:" ";clear:both;height:0}.education__list{width:100%}.education__specialization{margin:10px 0 !important}.education__school{font-size:13px;font-style:italic;color:#999;margin-bottom:0}.education__date{font-size:13px;font-style:italic;margin-bottom:10px;color:#999}.education__hr{height:1px;background-color:#eff0f2;width:100%}.education__form-item:after{visibility:hidden;display:block;font-size:0;content:" ";clear:both;height:0}.app{background-color:#eee;min-height:100vh;padding:20px 0}

